export default {
	name: 'ControlBar',
	props: {
		btnType: {
			type: Number,
			default () {
				return 0
			}
		}
	},
	data() {
		return {
			onTouch: -1
		}
	},
	methods: {
		tapYes() {
			this.$emit('tapYes')
		},
		tapNo() {
			this.$emit('tapNo')
		},
		touchStar(e) {
			this.onTouch = e.currentTarget.dataset.id
		},
		touchEnd() {
			this.onTouch = -1
		}
	}

}
